import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { Heading } from '@Green-Dot-Corporation/eureka-ui-typography';
import WindowContainer from '../window-container/WindowContainer';
import WarningAlert from '../svg/WarningAlert';
import Success from '../svg/Success';
import './styles/generic-message.scss';

class GenericMessage extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        windowTitle: PropTypes.string,
        shouldShowMinimizeBtn: PropTypes.bool,
        icon: PropTypes.string,
        renderIcon: PropTypes.func,
        headText: PropTypes.string,
        subText: PropTypes.string,
    };

    render() {
        const {
            contextCls,
            windowTitle,
            shouldShowMinimizeBtn,
            headText,
            subText,
        } = this.props;

        return (
            <WindowContainer
                contextCls={cx(this.baseCls, contextCls)}
                title={windowTitle}
                shouldShowMinimizeBtn={shouldShowMinimizeBtn}
                onMinimizeBtnClick={this.handleMinimizeBtnClick}
                shouldShowCloseBtn
                onCloseBtnClick={this.handleCloseBtnClick}
            >
                <div className={`${this.baseCls}__message-content`}>
                    {this.renderSvg()}

                    <Heading
                        contextCls={`${this.baseCls}__headText`}
                        headingLvl={2}
                    >
                        {headText}
                    </Heading>

                    <p className={`${this.baseCls}__subText`}>{subText}</p>
                </div>
            </WindowContainer>
        );
    }

    renderSvg = () => {
        const { icon, renderIcon } = this.props;

        if (renderIcon) {
            return renderIcon();
        }

        return icon === 'WarningAlert' ? <WarningAlert /> : <Success />;
    };

    handleCloseBtnClick = () => {
        // TODO: will replace by real close conversation session when Gateway API is ready
        console.log('close btn click');
    };

    handleMinimizeBtnClick = () => {
        // TODO: will replace by minimize the window
        console.log('min btn click');
    };

    baseCls = 'web-chat-generic-message';
}

export default GenericMessage;
