import React, { PureComponent, useState } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { Heading } from '@Green-Dot-Corporation/eureka-ui-typography';
import { Button, VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';
import WindowContainer from '../window-container/WindowContainer';
import PaperAirplane from '../svg/PaperAirplane';
import './styles/leave-message.scss';

const { PRIMARY } = VARIANTS;

class LeaveMessage extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
    };

    constructor(props) {
        super(props);
        this.state = {
            value: '',
            isBtnEnabled: false,
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    render() {
        const { contextCls } = this.props;
        const { value, isBtnEnabled } = this.state;

        return (
            <WindowContainer
                contextCls={cx(this.baseCls, contextCls)}
                shouldShowCloseBtn
                onCloseBtnClick={this.handleCloseBtnClick}
            >
                <div className={`${this.baseCls}__leave-message-content`}>
                    <PaperAirplane />

                    <Heading
                        contextCls={`${this.baseCls}__headText`}
                        headingLvl={2}
                    >
                        Leave a message
                    </Heading>

                    <p className={`${this.baseCls}__subText`}>
                        Please leave a message and we will get back to you as
                        soon as we can.
                    </p>

                    <form onSubmit={this.handleSubmit}>
                        <label htmlFor="textarea">
                            <textarea
                                name="textarea"
                                rows="6"
                                cols="43"
                                placeholder="Message"
                                className={`${this.baseCls}__textarea`}
                                value={value}
                                onChange={this.handleChange}
                            />
                        </label>
                        {/* <input type="submit" value="提交" /> */}
                    </form>

                    <Button
                        contextCls={cx(`${this.baseCls}__btn`)}
                        text="Send message"
                        variant={PRIMARY}
                        onClick={this.handleBtnClick}
                        // TODO: replace the aria-label title when localization available
                        aria-label="Send message"
                        isDisabled={!isBtnEnabled}
                    />
                </div>
            </WindowContainer>
        );
    }

    handleCloseBtnClick = () => {
        // TODO: will replace by real close conversation session when Gateway API is ready
        console.log('close btn click');
    };

    handleBtnClick = () => {
        // TODO: will replace by real close conversation session when Gateway API is ready
        console.log('Send message btn clicked');
    };

    handleChange(event) {
        const inputValue = event.target.value.trim();

        if (inputValue) {
            this.setState({ value: event.target.value });
            this.setState({ isBtnEnabled: true });
        }
    }

    handleSubmit(event) {
        alert(`提交的文章: ${this.value}`);
        event.preventDefault();
    }

    baseCls = 'web-chat-leave-message';
}

export default LeaveMessage;
