import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { Heading } from '@Green-Dot-Corporation/eureka-ui-typography';
import { Button, VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';
import WindowContainer from '../window-container/WindowContainer';
import Clock from '../svg/Clock';
import './styles/hoop.scss';

const { SECONDARY } = VARIANTS;

class Hoop extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        isLeaveMsgEnabled: PropTypes.bool,
    };

    render() {
        const { contextCls, isLeaveMsgEnabled } = this.props;

        const isShowBtn = isLeaveMsgEnabled;

        return (
            <WindowContainer
                contextCls={cx(this.baseCls, contextCls)}
                shouldShowCloseBtn
                onCloseBtnClick={this.handleCloseBtnClick}
            >
                <div className={`${this.baseCls}__hoop-content`}>
                    <Clock />

                    <Heading
                        contextCls={`${this.baseCls}__headText`}
                        headingLvl={2}
                    >
                        Chat support hours are 9-6pm PST
                    </Heading>

                    <p className={`${this.baseCls}__subText`}>
                        {isLeaveMsgEnabled
                            ? `Please try again during normal business hours or leave a message and we will get back to you.`
                            : `Please try again during normal business hours.`}
                    </p>

                    {isShowBtn && this.renderBtn()}
                </div>
            </WindowContainer>
        );
    }

    renderBtn = () => {
        return (
            <Button
                contextCls={cx(`${this.baseCls}__btn`)}
                text="Leave a message"
                variant={SECONDARY}
                onClick={this.handleBtnClick}
                // TODO: replace the aria-label title when localization available
                aria-label="Leave a message"
            />
        );
    };

    handleCloseBtnClick = () => {
        // TODO: will replace by real close conversation session when Gateway API is ready
        console.log('close btn click');
    };

    handleBtnClick = () => {
        // TODO: will replace by real close conversation session when Gateway API is ready
        console.log('Leave a message btn clicked');
    };

    baseCls = 'web-chat-hoop';
}

export default Hoop;
